import * as fs from "fs";
import * as https from "https";
// import * as mkdirp from 'mkdirp';
import * as path from "path";
import * as tmp from "tmp";
import { parse as parseUrl } from "url";
// import * as yauzl from 'yauzl';
import * as util from "./common";
import { Logger } from "./logger";
import { PlatformInformation } from "./platform";
import { getProxyAgent } from "./proxy";

import pump from "pump";
import * as tfs from "tar-fs";
import * as zlib from "zlib";

export interface Package {
  description: string;
  url: string;
  fallbackUrl?: string;
  installPath?: string;
  platforms: string[];
  architectures: string[];
  binaries: string[];
  tmpFile: tmp.SynchrounousResult;

  // Path to use to test if the package has already been installed
  installTestPath?: string;
}

export interface Status {
  setMessage: (text: string) => void;
  setDetail: (text: string) => void;
}

export class PackageError extends Error {
  // Do not put PII (personally identifiable information) in the 'message' field as it will be logged to telemetry
  constructor(
    public message: string,
    public pkg: Package | undefined = undefined,
    public innerError: any = undefined
  ) {
    super(message);
  }
}

export class PackageManager {
  private allPackages: Package[] | undefined;

  public constructor(
    private platformInfo: PlatformInformation,
    private packageJSON: any
  ) {
    // Ensure our temp files get cleaned up in case of error.
    tmp.setGracefulCleanup();
  }

  public DownloadPackages(
    logger: Logger,
    status: Status,
    proxy: string,
    strictSSL: boolean
  ): Promise<void> {
    return this.GetPackages().then((packages) => {
      return util.buildPromiseChain(packages, (pkg) =>
        maybeDownloadPackage(pkg, logger, status, proxy, strictSSL)
      );
    });
  }

  public InstallPackages(logger: Logger, status: Status): Promise<void> {
    return this.GetPackages().then((packages) => {
      return util.buildPromiseChain(packages, (pkg) =>
        installPackage(pkg, logger, status)
      );
    });
  }

  private GetAllPackages(): Promise<Package[]> {
    return new Promise<Package[]>((resolve, reject) => {
      if (this.allPackages) {
        resolve(this.allPackages);
      } else if (this.packageJSON.runtimeDependencies) {
        this.allPackages = <Package[]>this.packageJSON.runtimeDependencies;

        // Convert relative binary paths to absolute
        for (let pkg of this.allPackages) {
          if (pkg.binaries) {
            pkg.binaries = pkg.binaries.map((value) =>
              path.resolve(getBaseInstallPath(pkg), value)
            );
          }
        }

        resolve(this.allPackages);
      } else {
        reject(new PackageError("Package manifest does not exist."));
      }
    });
  }

  private GetPackages(): Promise<Package[]> {
    return this.GetAllPackages().then((list) => {
      return list.filter((pkg) => {
        if (
          pkg.architectures &&
          pkg.architectures.indexOf(this.platformInfo.architecture) === -1
        ) {
          return false;
        }

        if (
          pkg.platforms &&
          pkg.platforms.indexOf(this.platformInfo.platform) === -1
        ) {
          return false;
        }

        return true;
      });
    });
  }
}

function getBaseInstallPath(pkg: Package): string {
  let basePath = util.getExtensionPath();
  if (pkg.installPath) {
    basePath = path.join(basePath, pkg.installPath);
  }

  return basePath;
}

function getNoopStatus(): Status {
  return {
    setMessage: (text) => {},
    setDetail: (text) => {},
  };
}

function maybeDownloadPackage(
  pkg: Package,
  logger: Logger,
  status: Status,
  proxy: string,
  strictSSL: boolean
): Promise<void> {
  return doesPackageTestPathExist(pkg).then((exists: boolean) => {
    if (!exists) {
      return downloadPackage(pkg, logger, status, proxy, strictSSL);
    } else {
      logger.appendLine(
        `Skipping package '${pkg.description}' (already downloaded).`
      );
    }
  });
}

function downloadPackage(
  pkg: Package,
  logger: Logger,
  status: Status,
  proxy: string,
  strictSSL: boolean
): Promise<void> {
  status = status || getNoopStatus();

  logger.append(`Downloading package '${pkg.description}' `);

  status.setMessage("$(cloud-download) Downloading packages");
  status.setDetail(`Downloading package '${pkg.description}'...`);

  return new Promise<tmp.SynchrounousResult>((resolve, reject) => {
    tmp.file(
      { prefix: "package-" },
      (err: any, path: any, fd: any, cleanupCallback: any) => {
        if (err) {
          return reject(new PackageError("Error from tmp.file", pkg, err));
        }

        resolve(<tmp.SynchrounousResult>{
          name: path,
          fd: fd,
          removeCallback: cleanupCallback,
        });
      }
    );
  }).then((tmpResult) => {
    pkg.tmpFile = tmpResult;

    let result = downloadFile(
      pkg.url,
      pkg,
      logger,
      status,
      proxy,
      strictSSL
    ).then(() => logger.appendLine(" Done!"));

    // If the package has a fallback Url, and downloading from the primary Url failed, try again from
    // the fallback. This is used for debugger packages as some users have had issues downloading from
    // the CDN link.
    if (pkg.fallbackUrl) {
      result = result.catch((primaryUrlError) => {
        logger.append(`\tRetrying from '${pkg.fallbackUrl}' `);
        return downloadFile(
          pkg.fallbackUrl!,
          pkg,
          logger,
          status,
          proxy,
          strictSSL
        )
          .then(() => logger.appendLine(" Done!"))
          .catch(() => primaryUrlError);
      });
    }

    return result;
  });
}

function downloadFile(
  urlString: string,
  pkg: Package,
  logger: Logger,
  status: Status,
  proxy: string,
  strictSSL: boolean
): Promise<void> {
  const url = parseUrl(urlString);

  const options: https.RequestOptions = {
    host: url.host,
    path: url.path,
    agent: getProxyAgent(url, proxy, strictSSL),
    rejectUnauthorized: util.isBoolean(strictSSL) ? strictSSL : true,
  };

  return new Promise<void>((resolve, reject) => {
    if (!pkg.tmpFile || pkg.tmpFile.fd === 0) {
      return reject(
        new PackageError("Temporary package file unavailable", pkg)
      );
    }

    let request = https.request(options, (response) => {
      if (response.statusCode === 301 || response.statusCode === 302) {
        // Redirect - download from new location
        return resolve(
          downloadFile(
            response.headers.location!,
            pkg,
            logger,
            status,
            proxy,
            strictSSL
          )
        );
      }

      if (response.statusCode !== 200) {
        // Download failed - print error message
        logger.appendLine(`failed (error code '${response.statusCode}')`);
        return reject(new PackageError(response.statusCode!.toString(), pkg));
      }

      // Downloading - hook up events
      let packageSize = parseInt(response.headers["content-length"]!, 10);
      let downloadedBytes = 0;
      let downloadPercentage = 0;
      let dots = 0;
      let tmpFile = fs.createWriteStream("", { fd: pkg.tmpFile.fd });

      logger.append(`(${Math.ceil(packageSize / 1024)} KB) `);

      response.on("data", (data) => {
        downloadedBytes += data.length;

        // Update status bar item with percentage
        let newPercentage = Math.ceil(100 * (downloadedBytes / packageSize));
        if (newPercentage !== downloadPercentage) {
          status.setDetail(
            `Downloading package '${pkg.description}'... ${downloadPercentage}%`
          );
          downloadPercentage = newPercentage;
        }

        // Update dots after package name in output console
        let newDots = Math.ceil(downloadPercentage / 5);
        if (newDots > dots) {
          logger.append(".".repeat(newDots - dots));
          dots = newDots;
        }
      });

      response.on("end", () => {
        resolve();
      });

      response.on("error", (err) => {
        reject(
          new PackageError(`Reponse error: ${err.message || "NONE"}`, pkg, err)
        );
      });

      // Begin piping data from the response to the package file
      response.pipe(tmpFile, { end: false });
    });

    request.on("error", (err) => {
      reject(
        new PackageError(`Request error: ${err.message || "NONE"}`, pkg, err)
      );
    });

    // Execute the request
    request.end();
  });
}

function installPackage(
  pkg: Package,
  logger: Logger,
  status?: Status
): Promise<void> {
  if (!pkg.tmpFile) {
    // Download of this package was skipped, so there is nothing to install
    return Promise.resolve();
  }

  status = status || getNoopStatus();

  logger.appendLine(`Installing package '${pkg.description}'`);

  status.setMessage("$(desktop-download) Installing packages...");
  status.setDetail(`Installing package '${pkg.description}'`);

  return new Promise<void>((resolve, baseReject) => {
    const reject = (err: PackageError) => {
      // If anything goes wrong with unzip, make sure we delete the test path (if there is one)
      // so we will retry again later
      const testPath = getPackageTestPath(pkg);
      if (testPath) {
        fs.unlink(testPath, (unlinkErr) => {
          baseReject(err);
        });
      } else {
        baseReject(err);
      }
    };

    if (pkg.tmpFile.fd === 0) {
      return reject(new PackageError("Downloaded file unavailable", pkg));
    }

    let binaryName: any;
    let options = {
      readable: true,
      writable: true,
      hardlinkAsFilesFallback: true,
    };
    let extract = tfs
      .extract(getBaseInstallPath(pkg), options)
      .on("entry", function (entry: any) {
        if (/\.node$/i.test(entry.name)) {
          binaryName = entry.name;
        }
      });

    pump(
      fs.createReadStream(pkg.tmpFile.name),
      zlib.createGunzip(),
      extract,
      function (err: any) {
        if (err) {
          return reject(new PackageError("Immediate zip file error", pkg, err));
        }

        if (binaryName) {
          let absoluteEntryPath = path.resolve(
            getBaseInstallPath(pkg) || ".",
            binaryName
          );

          logger.appendLine("unpack resolved to " + absoluteEntryPath);

          try {
            require(absoluteEntryPath);
          } catch (err) {
            return reject(new PackageError("require test error", pkg, err));
          }
          logger.appendLine(
            "unpack required " + absoluteEntryPath + " successfully"
          );

          resolve();
        }
      }
    );
  }).then(() => {
    // Clean up temp file
    pkg.tmpFile.removeCallback();
  });
}

function doesPackageTestPathExist(pkg: Package): Promise<boolean> {
  const testPath = getPackageTestPath(pkg);
  if (testPath) {
    return util.fileExists(testPath);
  } else {
    return Promise.resolve(false);
  }
}

function getPackageTestPath(pkg: Package): string | undefined {
  if (pkg.installTestPath) {
    return path.join(util.getExtensionPath(), pkg.installTestPath);
  } else {
    return undefined;
  }
}
